@import "app/assets/vars/vars";
.menu__link {
	color: $secColor;
	text-decoration: none;
	text-transform: uppercase;
	font-family: $mainFont;
	font-size: 14px;
	transition: .2s;
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 10px;
	padding-right: 10px;
	border-top: 1px solid rgba(0, 0, 0, .0);
	border-bottom: 1px solid rgba(0, 0, 0, .0);
	&:hover {
		color: $mainColor;
		border-top: 1px solid $mainColor;
		border-bottom: 1px solid $mainColor;
		padding-top: 10px;
		padding-bottom: 10px; } }
